@custom-media --small-viewport (width <= 640px) {}:root {
  --baseColor: #657b83;
  --bgColor: #fdf6e3;
  --headsColor: #eee8d5;
  --emphColor: #586e75;
  --commentColor: #93a1a1;
  --inputBg: color(#fdf6e3 tint(4%));
  --inputBgDisabled: #93a1a1;
  --inputColor: #073642;
  --inputBorder: #93a1a1;
  --inputColorPlaceholder: color(#657b83 tint(30%));
  --buttonHover: color(#eee8d5 tint(10%)); }

:root.dark {
  --baseColor: #839496;
  --bgColor: #002b36;
  --headsColor: #073642;
  --emphColor: #93a1a1;
  --commentColor: #586e75;
  --inputBg: #073642;
  --inputBgDisabled: #586e75;
  --inputColor: #eee8d5;
  --inputBorder: #586e75;
  --inputColorPlaceholder: color(#839496 shade(30%));
  --buttonHover: color(#073642 shade(10%)); }

html {
  font-size: responsive 18px 22px;
  font-range: 420px 980px;
  background-color: var(--headsColor);
  color: var(--baseColor); }

body {
  font-family: system-ui; }

a {
  color: #b58900; }

a:hover {
  color: #cb4b16;
  text-decoration: none; }

pre {
  font-family: system-ui;
  font-size: 0.8125rem;
  background-color: var(--bgColor);
  color: var(--baseColor);
  border: 1px solid var(--commentColor);
  padding: 0.8125rem;
  box-shadow: 5px 5px 8px var(--headsColor);
  white-space: pre-wrap; }
  pre code {
    background-color: var(--bgColor); }

h1, h2, h3, h4, h5, h6 {
  color: #859900; }

h1 {
  font-size: 2.4rem; }

h2 {
  font-size: 2.0rem; }

h3 {
  font-size: 1.8rem; }

h4 {
  font-size: 1.4rem; }

h5 {
  font-size: 1.3rem; }

h6 {
  font-size: 1.15rem; }

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  appearance: none;
  margin: 0; }

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0; }

#app-root {
  padding: 0.8rem; }
